import React from "react";
import { useNavigate } from "react-router-dom";

import { MyCard } from "./MyCard";
import palmierImage from "../assets/palmier.jpg";
import conception from "../assets/conception.png";

import "../App.css";

export default function Home() {
  const navigate = useNavigate();
  return (
    <div
      style={{
        backgroundImage: `url(${palmierImage})`,
        backgroundSize: "cover",
        height: "100vh",
      }}
    >
      {" "}
      <div
        style={{
          textAlign: "center",
          color: "white",
          marginBottom: "25px",
          fontSize: "50px",
        }}
      >
        Liste des parametres
      </div>
      <div className="d-flex justify-content-between">
        <MyCard
          text=""
          title="Besoin net d'irrigation"
          handleClick={() => navigate("/dose")}
          image="data:image/jpeg;base64,"
        />
        <MyCard
          handleClick={() => navigate("/temps")}
          text=""
          title="Temps d'irregation"
          image="data:image/png;base64,"
        />
        <MyCard
          text=""
          title="Conception"
          handleClick={() => navigate("/conception")}
          image={conception}
        />

        {/* <Button>Besoin net d'irrigation</Button>
        <Button>Temps d'irregation</Button>
        <Button
          onClick={() => {
            navigate("/conception");
          }}
        >
          Conception
        </Button> */}
      </div>
    </div>
  );
}
